<template>
  <div id="page-user-edit">
    <vx-card>
      	<div slot="no-body" class="tabs-container px-6 pt-6">
			<vs-tabs class="tab-action-btn-fill-conatiner">
				<!-- Account Tab -->
				<vs-tab label="Account" icon-pack="feather" icon="icon-user">
					<div class="tab-text">
						<div id="user-edit-tab-info">
							<!-- Avatar Row -->
							<div class="vx-row">
								<div class="vx-col">
									<div class="flex items-start flex-col sm:flex-row">
									<img src="/img/avatar-s-11.1a620230.jpg" class="mr-8 rounded avatar-col" />
									<div>
										<p class="text-lg font-medium mb-2 mt-4 sm:mt-0">{{ dispatcherData.profile.firstName }} {{ dispatcherData.profile.lastName }}</p>
										<input type="file" class="hidden" ref="update_avatar_input" @change="update_avatar" accept="image/*">
										<vs-button class="mr-4 mb-4" @click="$refs.update_avatar_input.click()">Change photo</vs-button>
										<vs-button type="border" color="danger">Delete photo</vs-button>
									</div>
									</div>
								</div>
							</div>
							<!-- Content Row -->
							<div class="vx-row">
								<div class="vx-col md:w-1/2 w-full">
									<vs-input class="w-full mt-4" label="Username" v-model="userData.username" name="username" />
									<vs-input class="w-full mt-4" label="First name" v-model="dispatcherData.profile.firstName" name="firstname" />
									<vs-input class="w-full mt-4" label="Last name" v-model="dispatcherData.profile.lastName" name="lastname" />
								</div>
								<div class="vx-col md:w-1/2 w-full">
									<vs-input class="w-full mt-4" label="Email" v-model="dispatcherData.profile.contact.email" type="email" name="email" />
									<vs-input class="w-full mt-4" label="Phone" v-model="dispatcherData.profile.contact.phone" type="email" name="phone" />
									<vs-input class="w-full mt-4" label="Address" v-model="dispatcherData.profile.contact.address" type="email" name="address" />
									<!-- <div class="mt-4">
										<label class="vs-input--label">Status</label>
										<v-select :clearable="false" :options="statusOptions" v-validate="'required'" name="status" :dir="$vs.rtl ? 'rtl' : 'ltr'" />
										<span class="text-danger text-sm"  v-show="errors.has('status')">{{ errors.first('status') }}</span>
									</div>
									<div class="mt-4">
										<label class="vs-input--label">Role</label>
										<v-select :clearable="false" :options="roleOptions" v-validate="'required'" name="role" :dir="$vs.rtl ? 'rtl' : 'ltr'" />
										<span class="text-danger text-sm"  v-show="errors.has('role')">{{ errors.first('role') }}</span>
									</div>
									<vs-input class="w-full mt-4" label="Company" v-validate="'alpha_spaces'" name="company" />
									<span class="text-danger text-sm"  v-show="errors.has('company')">{{ errors.first('company') }}</span> -->
								</div>
							</div>
							<!-- Permissions -->
							<vx-card class="mt-base" no-shadow card-border>
								<div class="vx-row">
									<div class="vx-col w-full">
									<div class="flex items-end px-3">
										<feather-icon svgClasses="w-6 h-6" icon="LockIcon" class="mr-2" />
										<span class="font-medium text-lg leading-none">Permissions</span>
									</div>
									<vs-divider />
									</div>
								</div>

								<div class="block overflow-x-auto">
									<table class="w-full">
									<tr>
										<th class="font-semibold text-base text-left px-3 py-2" v-for="heading in ['Module', 'Read', 'Write', 'Create', 'Delete']" :key="heading">{{ heading }}</th>
									</tr>

									<tr v-for="(permission, index) in permissions" :key="index">
										<td class="px-3 py-2">{{ permission.entity }}</td>
										<td class="px-3 py-2" v-for="item in permission.crud" :key="item.id">
											<vs-checkbox v-model="item.active"/>
										</td>
									</tr>
									</table>
									{{ perm }}
								</div>
							</vx-card>
							<!-- Save & Reset Button -->
							<div class="vx-row">
								<div class="vx-col w-full">
									<div class="mt-8 flex flex-wrap items-center justify-end">
										<vs-button class="ml-auto mt-2" @click="save_changes" :disabled="!validateForm">Save Changes</vs-button>
										<vs-button class="ml-4 mt-2" type="border" color="warning" @click="reset_data">Reset</vs-button>
									</div>
								</div>
							</div>
						</div>
					</div>
				</vs-tab>
				<!-- Settings Tab -->
				<vs-tab label="Settings" icon-pack="feather" icon="icon-settings">
					<div class="tab-text">
						<h3 class="flex items-center">
							<feather-icon svgClasses="w-6 h-6" icon="HeadphonesIcon" class="mr-2" />
							<span class="font-medium text-lg leading-none">Account</span>
						</h3>
						<vs-divider />
						<div class="vx-row pb-10">
							<div class="vx-col sm:w-1/2 w-full">
								<div class="flex items-center my-5">
									<vs-button color="primary" type="filled">Primary</vs-button>
									<span class="ml-4">Google authorization successful: namesurname@gmail.com</span>
								</div>
								<vs-input label="Email HTML Signature" class="w-full mb-4" />
							</div>
							<div class="vx-col sm:w-1/2 w-full">
								<vs-input label="Bid intercept email" class="w-full mb-4" />
								<vs-input label="Email text signature" class="w-full mb-4" />
							</div>
						</div>
						<h3 class="flex items-center">
							<feather-icon svgClasses="w-6 h-6" icon="MailIcon" class="mr-2" />
							<span class="font-medium text-lg leading-none">Email settings</span>
						</h3>
						<vs-divider />
						<div class="vx-row pb-10">
							<div class="vx-col sm:w-1/2 w-full">
								<div class="flex items-center my-5">
									<vs-button color="primary" type="filled">Primary</vs-button>
									<span class="ml-4">Google authorization successful: namesurname@gmail.com</span>
								</div>
								<vs-input label="Company name" class="w-full mb-4" />
								<vs-input label="MC Number" class="w-full mb-4" />
							</div>
							<div class="vx-col sm:w-1/2 w-full">
								<vs-textarea label="Bid email template" class="w-full my-4 ta-height" />
							</div>
						</div>
						<h3 class="flex items-center">
							<feather-icon svgClasses="w-6 h-6" icon="CopyIcon" class="mr-2" />
							<span class="font-medium text-lg leading-none">Match settings</span>
						</h3>
						<vs-divider />
						<div class="vx-row pb-10">
							<div class="vx-col sm:w-1/2 w-full mt-4">
								<vs-checkbox class="mb-4"><span class="pl-4">Use ON LOAD vehicles</span></vs-checkbox>
								<vs-checkbox class="mb-4"><span class="pl-4">Use vehicle average speed filter</span></vs-checkbox>
								<vs-checkbox class="mb-4"><span class="pl-4">Use vehicle size filter</span></vs-checkbox>
								<div class="flex items-center mt-10">
									<vs-input label="Max vehicle fly radius (miles)" class="w-full pr-4" />
									<vs-input label="Match count" class="w-full pl-4" />
								</div>
							</div>
						</div>
						<!-- Save & Reset Button -->
						<div class="vx-row">
							<div class="vx-col w-full">
								<div class="mt-4 flex flex-wrap items-center justify-end">
									<vs-button class="ml-auto" @click="save_changes" :disabled="!validateForm">Save Settings</vs-button>
									<vs-button class="ml-4" type="border" color="warning" @click="reset_data">Discard Changes</vs-button>
								</div>
							</div>
						</div>
					</div>
				</vs-tab>
			</vs-tabs>
      	</div>
    </vx-card>
  </div>
</template>

<script>
import vSelect from 'vue-select'

export default {
	components: {
    	vSelect
	},
	data() {
    return {
		userData: {},
		dispatcherData: {},
		entities: ["User", "Role", "Permission", "UserRole", "UserPermission", "RolePermission", "Dispatcher", "Driver", "HumanResource", "Owner", "Load", "Provider", "Vehicle", "Bid", "SuitableVehicle"],

		statusOptions: [
			{ label: "Active",  value: "active" },
			{ label: "Blocked",  value: "blocked" },
			{ label: "Deactivated",  value: "deactivated" },
		],
		roleOptions: [
			{ label: "Admin",  value: "admin" },
			{ label: "User",  value: "user" },
			{ label: "Staff",  value: "staff" },
		],

		permissionsAPI: [1, 2, 4, 7, 10, 12],

		permissions: [
			{
				entity: "Users",
				crud: [
					{
						id: 1,
						name: "CreateUser",
						active: false
					},
					{
						id: 2,
						name: "ReadUser",
						active: false
					},
					{
						id: 3,
						name: "UpdateUser",
						active: false
					},
					{
						id: 4,
						name: "DeleteUser",
						active: false
					}
				],
			},
			{
				entity: "Role",
				crud: [
					{
						id: 5,
						name: "CreateRole",
						active: false
					},
					{
						id: 6,
						name: "ReadRole",
						active: false
					},
					{
						id: 7,
						name: "UpdateRole",
						active: false
					},
					{
						id: 8,
						name: "DeleteRole",
						active: false
					}
				],
			},
			{
				entity: "Permission",
				crud: [
					{
						id: 9,
						name: "CreatePermission",
						active: false
					},
					{
						id: 10,
						name: "ReadPermission",
						active: false
					},
					{
						id: 11,
						name: "UpdatePermission",
						active: false
					},
					{
						id: 12,
						name: "DeletePermission",
						active: false
					}
				],
			}
		]
    }
  },
  computed: {
	  perm () {
		let result = []
		const temp = this.permissions.map(item => {
			return item.crud.map(i => {
				if(i.active) return i.id
			}).filter(item => item)
		})

		temp.forEach(element => {
			result = [...result, ...element]
		})

		return result
	  },

    status_local: {
      get() {
        return { label: this.capitalize(this.data_local.status),  value: this.data_local.status  }
      },
      set(obj) {
        this.data_local.status = obj.value
      }
    },
    role_local: {
      get() {
        return { label: this.capitalize(this.data_local.role),  value: this.data_local.role  }
      },
      set(obj) {
        this.data_local.role = obj.value
      }
    },
    validateForm() {
      return !this.errors.any()
    }
  },
  methods: {
    capitalize(str) {
      return str.slice(0,1).toUpperCase() + str.slice(1,str.length)
    },
    save_changes() {
		// if(!this.validateForm) return
		this.$store.dispatch("user/userUpdate", this.userData)
		this.$store.dispatch("user/dispatcherUpdate", this.dispatcherData)
		this.$router.push("/dispatchers/view")
    },
    reset_data() {
      this.userData = JSON.parse(JSON.stringify(this.$store.getters["user/getUser"]))
      this.dispatcherData = JSON.parse(JSON.stringify(this.$store.getters["user/getDispatcher"]))
    },
    update_avatar() {
      // You can update avatar Here
      // For reference you can check dataList example
      // We haven't integrated it here, because data isn't saved in DB
    }
  },
  created() {
	// Copy objects
	this.userData = JSON.parse(JSON.stringify(this.$store.getters["user/getUser"]))
	this.dispatcherData = JSON.parse(JSON.stringify(this.$store.getters["user/getDispatcher"]))

	this.permissions.forEach(item => {
		item.crud.forEach(i => {
			if(this.permissionsAPI.includes(i.id)){
				i.active = true
			}
		})
	})
  }
}
</script>
<style lang="scss" scoped>
	.avatar-col {
		width: 8.5rem;
	}
	.ta-height{
		height: 190px;
	}
</style>